@import url("https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  max-width: 100vw;
}
